body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


.InputAndResults {
	white-space:nowrap;
}

.Results {
	overflow-y: hidden;
	background-color: rgb(105,105,105);
	color: white;
	text-align: left;
	vertical-align:top;
	width: 29%;
	display: inline-block;
	resize: none;
}

.FormulaInput {
	width: 69%;
	resize: vertical;
	display: inline-block;
}

#results .value {
    font-weight:  bold;
    color: green;
}

#results .error {
    font-weight:  bold;
    color: red;
}

#constants {
    display: inline-block;
    margin: 5px 25px 5px 25px;
}

#constants table {
    border-collapse: collapse;
    margin: 25px 0;
    font-size: 0.9em;
    font-family: sans-serif;
    min-width: 400px;
    box-shadow: 0 0 20px rgba(0, 0, 0, 0.15);
}

#constants thead tr {
    background-color: #3d5c56;
    color: #ffffff;
    text-align: left;
}

#constants th,
#constants td {
    padding: 5px;
}

#constants tbody tr {
    border-bottom: 1px solid #dddddd;
}

#constants tbody tr:nth-of-type(even) {
    background-color: #f3f3f3;
}

#constants tbody tr:last-of-type {
    border-bottom: 2px solid #009879;
}

#variables {
    display: inline-block;
    margin: 5px 25px 5px 25px;
}

#variables table {
    border-collapse: collapse;
    margin: 25px 0;
    font-size: 0.9em;
    font-family: sans-serif;
    min-width: 400px;
    box-shadow: 0 0 20px rgba(0, 0, 0, 0.15);
}

#variables thead tr {
    background-color: #009879;
    color: #ffffff;
    text-align: left;
}

#variables th,
#variables td {
    padding: 5px;
}

#variables tbody tr {
    border-bottom: 1px solid #dddddd;
}

#variables tbody tr:nth-of-type(even) {
    background-color: #f3f3f3;
}

#variables tbody tr:last-of-type {
    border-bottom: 2px solid #009879;
}

#footer {
    text-align: right;
}

#footer a {
    font-style: italic;
    margin: 25px;
    font-size: 0.7em;
}

